import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';

import textField from 'muse-ui/textField';
import checkbox from 'muse-ui/checkbox';

import Picker from '~/components/picker/index.vue';
import { IPickerEvent, IPickerItem } from '~/components/picker';

import { User } from '~/entity/user';
import { WOMAN_AVATAR, MAN_AVATAR, MODAL_0, CIVIL_STATE_OPTIONS } from '~/config';
import { IRegisterParams } from '~/entity/route_params';
import Utils from '~/utils';
import Api from '~/api';
import { ToastMsg } from '~/entity/toast';
import { CACHE_NAME } from '~/cache';

const years = Utils.getContinuousArray(1900, new Date().getFullYear());
const months = Utils.getContinuousArray(1, 12);

@Component({
    components: {
        'mu-text-field': textField,
        'picker': Picker,
        'mu-checkbox': checkbox
    }
})
export default class RegisterComp extends Vue {

    phone: string = '13925210768';
    
    sex: number = 2; // 默认女性

    realname: string = '';

    birthday: string = null;

    civilState: string = null;

    isAgree = true;

    /* picker 组件 */
    pickerTheme: string = null;

    pickerType: string = null;

    pickerData: IPickerItem[] = null;

    isActiveModal = true;

    @State(state => state.modal.MODAL_0)
    isShowModal;

    @Action('addModal')
    addModal;

    @Action('hideModal')
    hideModal;

    /************* */

    @Action('showToast')
    showToast;

    @Action('initUser')
    initUser;

    get avatar() {
        if (this.sex === 1) {
            return MAN_AVATAR;
        } else {
            return WOMAN_AVATAR;
        }
    }

    get buttonEnable () {
        if (this.realname.length > 0 && this.birthday && this.civilState
            && this.isAgree) {
            return true;
        } else {
            return false;
        }
    }

    // created() {
       
    // }

    activated() {
        const params: IRegisterParams = this['$route'].params;
        this.phone = params.phone;
        const sex = parseInt(params.sex, 10);
        if (sex === 2) {
            this.sex = sex;
        } else if (sex === 1) {
            this.sex = 1;
        } else {
            // 默认是女生
            this.sex = 2;
        }
        this.isActiveModal = true;
    }
    
    deactivated() {
        this.isActiveModal = false;
        if (this.isShowModal) {
            this.cancel();
        }
    }

    async register() {
        const realname = this.realname;
        const sex = this.sex;
        const birthday = this.birthday;
        const civilState = this.civilState;

        try {
            // 微信性别 1男 2女 0未知  后台性别 0男 1女
            const res = await Api.registerByWeChat(realname, sex - 1, birthday, civilState);
            // 注册成功，初始化个人数据，检查回调地址进行跳转，
            await this.initUser();
            const callbackUrl = Utils.getItem(CACHE_NAME.callbackUrl, false) || '/tab/ai';
            this['$router'].push({
                path: callbackUrl
            });
            
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        }
    }

    /**
     * 显示出生日期
     */
    showBirdth() {
        const defaultBirth = this.birthday || '1990-01-01';
        this.pickerData = this.getBirthPickerData(defaultBirth);
        this.pickerTheme = 'birthday';
        this.pickerType = 'date';
        setTimeout(() => {
            this.addModal(MODAL_0);
        }, 100);
    }

    /**
     * 显示婚恋状态
     */
    showCivilState() {
        const defaultCivilState = this.civilState || '单身';
        this.pickerData = [
            {
                list: CIVIL_STATE_OPTIONS,
                currentIndex: CIVIL_STATE_OPTIONS.indexOf(defaultCivilState)
            }
        ];
        this.pickerTheme = 'civilState';
        this.pickerType = '';
        this.addModal(MODAL_0);
    }

    cancel() {
        if (this.isShowModal) {
            // history.back();
            this.hideModal();
        } 
    }

    confirm(event: IPickerEvent) {
        if (event.theme === 'birthday') {
            this.confirmBirthday(event);
        } else if (event.theme === 'civilState') {
            this.confirmCivilState(event);
        }
        this.cancel();
        // this.cancel();
    }

    confirmBirthday(event: IPickerEvent) {
        const value = event.values.join('-');
        console.log(value);
        this.birthday = value;
    }

    confirmCivilState(event: IPickerEvent) {
        const value = event.values[0];
        this.civilState = value;
    }

    getBirthPickerData(defaultBirth: string) {
        const arr = defaultBirth.split('-');
        const defaultYear = parseInt(arr[0], 10);
        const defaultMonth = parseInt(arr[1], 10);
        const defaultDay = parseInt(arr[2], 10);

        const days = Utils.getContinuousArray(1, Utils.getMaxDate(arr[0], arr[1]));

        return [
            {
                list: years,
                currentIndex: years.indexOf(defaultYear)
            },
            {
                list: months,
                currentIndex: months.indexOf(defaultMonth)
            },
            {
                list: days,
                currentIndex: days.indexOf(defaultDay)
            }
        ];
    }
}



